import React, { useState, useEffect } from 'react';
import NotFound from './components/404.jsx';
import Dashboard from './components/Dashboard.jsx';
import Example from './components/Example.jsx';
import SignIn from './components/SignIn.jsx';
import Layout from './layout';
//import Big from 'big.js';
import { Route, Routes } from 'react-router-dom'
var version = require('../package.json').version;

const App = ({ contract, currentUser, config, wallet, provider, lastTransaction, error, signer }) => {
  const [message, setMessage] = useState('');

  useEffect(() => {
      if (error){
        setMessage(decodeURI(error));
        window.history.pushState({}, "", window.location.origin + window.location.pathname + window.location.hash);
      }
      else if (lastTransaction && currentUser) {          
        getState(lastTransaction, currentUser.accountId);
        window.history.pushState({}, "", window.location.origin + window.location.pathname + window.location.hash);
      }

      async function getState(txHash, accountId) {
        const result = await provider.txStatus(txHash, accountId);
        const receiver = result.transaction.receiver_id;
        const method = result.transaction.actions[0].FunctionCall.method_name;
        let message;

        if(receiver === contract.contractId && method === "sample_method"){
          //retrieve messages based on transaction details
          message = result.receipts_outcome[0].outcome.logs.pop();
        }
        if(!message){
          //some default fallback
          message = "The transaction was successfull";
        }
        if(message){
          setMessage(message);
        }
      }
  }, [lastTransaction, error, currentUser, provider, contract.contractId]);

  const onSampleMethod = async () => {
    var ObjectToArray = function(json)
    {
      var str = JSON.stringify(json, null, 0);
      var ret = new Uint8Array(str.length);
      for (var i = 0; i < str.length; i++) {
        ret[i] = str.charCodeAt(i);
      }
      return ret
    };

    /*const message = ObjectToArray({
      topic: "Sun",
      adjective: "warm",
      id: '1'
    });
    const signature = await signer.signMessage(message, currentUser.accountId, config.networkId);
    signature.signature = Array.from(signature.signature);
    signature.publicKey.data = Array.from(signature.publicKey.data);
    const messageArray = Array.from(message)

    try{
      const requestOptions = {
          method: 'POST',
          mode: 'cors',
          headers: {"Content-Type": "application/json"},
          body: JSON.stringify({
            message: messageArray,
            signature: signature,
            accountId: currentUser.accountId
          })
      };
      const response = await fetch(config.backendUrl+'get-haiku', requestOptions);
      const haiku = await response.json();
      console.log(haiku);
    }
    catch (err){
      console.error(err);
    }*/    

    const message = ObjectToArray({
      haiku: 'The sun is up / And so am I / Ready for the day',
      id: '1',
      title: 'A Warm Sun'
    });
    const signature = await signer.signMessage(message, currentUser.accountId, config.networkId);
    signature.signature = Array.from(signature.signature);
    signature.publicKey.data = Array.from(signature.publicKey.data);
    const messageArray = Array.from(message)

    try{
      const requestOptions = {
          method: 'POST',
          mode: 'cors',
          headers: {"Content-Type": "application/json"},
          body: JSON.stringify({
            message: messageArray,
            signature: signature,
            accountId: currentUser.accountId
          })
      };
      const response = await fetch(config.backendUrl+'set-haiku', requestOptions);
      const image = await response.json();
      console.log(image);
    }
    catch (err){
      console.error(err);
    }
  }
  
  const signIn = () => {
    wallet.requestSignIn(
      {contractId: contract.contractId, //contract requesting access 
       methodNames: [contract.sample_method.name]}, //used methods
      'NEAR Challenge #8 - DAO Dashboard', //optional name
      null, //optional URL to redirect to if the sign in was successful
      null //optional URL to redirect to if the sign in was NOT successful
    );
  };

  const signOut = () => {
    wallet.signOut();
    window.location.reload(false);
  };

  const clearMessage = () => {
    setMessage('');
  };

  //cleanup styles Login/Logout Button Popup close button, Copyright at bottom, Buttons filled
  //add github build script

  if(!currentUser){
    return (
      <Routes>
        <Route path="/" element={<Layout currentUser={currentUser} signIn={signIn} signOut={signOut} clearMessage={clearMessage} message={message}/>}>
          <Route index element={<SignIn signIn={signIn} version={version} />}/>
          <Route path="*" element={<SignIn signIn={signIn} version={version} />}/>
        </Route>
      </Routes>
    );
  }
  
  return (
    <Routes>
      <Route path="/" element={<Layout currentUser={currentUser} signIn={signIn} signOut={signOut} clearMessage={clearMessage} message={message}/>}>
        <Route index element={<Dashboard version={version}/>}/>
        <Route path="example" element={<Example onSampleMethod={onSampleMethod} />}/>
        <Route path="*" element={<NotFound/>}/>
      </Route>
    </Routes>
  );
}

export default App;
